@import url("https://fonts.googleapis.com/css2?family=PT+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box; }
  *:before, *:after {
    box-sizing: border-box; }

:focus, :active {
  outline: none; }

a {
  text-decoration: none; }
  a:hover, a:visited {
    text-decoration: none; }
  a:focus, a:active {
    outline: none; }

aside, nav, footer, header, section {
  display: block; }

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-width: 320px;
  position: relative;
  width: 100%;
  color: #000; }

body {
  font-family: "Montserrat";
  font-size: 14px;
  line-height: 1; }
  body.lock {
    overflow: hidden; }
    @media (max-width: 767.98px) {
      body.lock {
        width: 100%;
        position: fixed;
        overflow: hidden; } }

input, button, textarea {
  font-family: "Montserrat"; }

input {
  display: none; }

button {
  cursor: pointer;
  padding: 0;
  border: 0; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit; }

/*------------------------------ </RESET, COMMON PARAMETERS> ------------------------------*/
/*------------------------------ <WRAPPER> ------------------------------*/
.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden; }

/*------------------------------ </WRAPPER> ------------------------------*/
/*------------------------------ <MAIN GRID> ------------------------------*/
.container {
  max-width: 1170px;
  margin: 0 auto;
  width: 100%; }
  @media (max-width: 1182px) {
    .container {
      max-width: 970px; } }
  @media (max-width: 992.98px) {
    .container {
      max-width: 750px; } }
  @media (max-width: 767.98px) {
    .container {
      max-width: none;
      padding: 0 10px; } }

